<template>
  <div class="view">
    <div class="list">
      <div
        class="item"
        v-for="item in userList"
        :key="item.id"
        @click="$router.push('/chatItem/'+item.id)"
        :data-uid="item.id"
      >
        <img class="item-avatar" :src="item.avatar" alt="头像">
        <div>
          <div class="item-name">{{ item.nickname }}</div>
          <div class="item-msg" v-text="getLastMsg(item)"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import store from 'store';
import { mapState } from 'vuex';
const userInfo = store.get('userInfo');

export default {
  data () {
    return {
    };
  },
  computed: {
    ...mapState({
      userList: state => state.chat.userList,
      msgList: state => state.chat.msgList,
      msgObj: state => state.chat.msgObj
    })
  },
  methods: {
    getMsgKey (user) {
      return `${userInfo.id}_${user.id}`;
    },
    getLastMsg (user) {
      const key = this.getMsgKey(user);
      return this.msgObj[key][0]['content'];
    }
  }
};
</script>

<style lang="scss" scoped>
.view {
  background-color: #eee;
}

.item {
  display: flex;
  overflow: hidden;
  font-size: 12px;
  padding: 6px 10px;
  background-color: #fff;
  border-bottom: 1px solid #eee;
}

.item-avatar {
  float: left;
  width: 32px;
  height: 32px;
  margin-right: 10px;
  border-radius: 50%;
}

.item-msg {
  color: #666;
}

.item-name {
  font-size: 13px;
  margin-bottom: 4px;
}
</style>



